.payment {
  background-color: white;
}

.payment__container > h1 {
  text-align: center;
  padding: 10px;
  font-weight: 400;
  background-color: rgb(234, 237, 237);
  border-bottom: 1px solid lightgray;
}

.payment__container > h1 a {
  text-decoration: none;
}

.payment__section {
  display: flex;
  padding: 20px;
  margin: 0 20px;
  border-bottom: 1px solid lightgray;
}

.payment__title {
  flex: 0.2;
}

.payment__address,
.payment__items,
.payment__details {
  flex: 0.8;
}

.payment__details {
  background-color: rgba(0, 0, 0, 0.048);
  padding: 20px;
  border-radius: 10px;
}

.address-form {
  margin-top: 40px;
  width: 70%;
}

.address-form input {
  width: 100%;
  margin-bottom: 10px;
  padding: 7px;
  border-radius: 5px;
  border-color: rgba(0, 0, 0, 0.207);
  font-size: 18px;
}

.payment--order-total {
  margin-top: 20px;
}

.payment--buy-now-btn {
  margin-top: 25px;
}
